import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { TableData } from 'components/common';
import { getStudents, downloadFileAdditional } from 'services/AdditionalAdmissionPapersPage';
import { Btn, BtnExport } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import FormSearchStudent from './FormSearchStudent';

class ReceiveAdmissionPapersPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  exportStudent = record => {
    downloadFileAdditional(record.studentId);
  };

  render() {
    const { reload, paramSearchClass } = this.state;
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'toScores',
      },
      {
        title: 'Xem chi tiết',
        dataIndex: 'detail',
        key: 'detail',
        render: (text, record) => (
          <Fragment>
            <Btn
              href={`#/additional-admission-papers/student/${record.studentId}`}
              title="Xem chi tiết"
            />
            <BtnExport onClick={() => this.exportStudent(record)} />
          </Fragment>
        ),
      },
    ];
    return (
      <Fragment>
        <Card title="Nhận giấy tờ bổ sung">
          <Row>
            <Col span={24}>
              <FormSearchStudent />
            </Col>

            <Col span={24}>
              <TableData
                reload={reload}
                formSearch
                service={getStudents}
                paramSearch={paramSearchClass}
                columns={columns}
                allowGetData={!!paramSearchClass.courseId}
                rowKey="id"
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ReceiveAdmissionPapersPage />
  </Page>
);



